import uniSection from '../../components/uni-section/uni-section.vue';
export default {
	components: { uniSection },
	data() {
		return {
			// 微信昵称
			nickname: '',
			center: {},
			// 主播数
			num: '',
			// 总收益
			all: '',
			// 可提现金额
			ablemoney: '',
			// 提现模态框的显示隐藏
			showMonmodal: false,
			// 用户信息
			userinfo: {},
			token: '',
			form: {
				cashMoney:''
			}
		};
	},
	onLoad() {
		this.getToken();
		this.getuserInfo();
	},
	onShow() {
		this.getMineinfo();
	},
	methods: {
		getToken() {
			this.token = uni.getStorageSync('token');
		},
		getMineinfo() {
			const that = this;
			uni.request({
				url: 'http://www.vzoyo.com/api/user/userCenter',
				header: { token: this.token },
				success(res) {
					// if(res.data.data.code == 1){
					that.ablemoney = res.data.data.able_money.toString();
					console.log(typeof res.data.data.anchor_num);
					that.num = res.data.data.anchor_num.toString();
					that.all = res.data.data.money;
					// }
				}
			});
		},
		getuserInfo() {
			this.userinfo = uni.getStorageSync('userinfo');
		},
		getMoney() {
			this.showMonmodal = true;
		},
		handleContact(e) {
			console.log(e.detail.path);
			console.log(e.detail.query);
		},
		closeModal() {
			this.showMonmodal = false;
		},
		// 关于模态框的函数
		withdraw() {
			console.log(12313123132313123131312)
			const that = this;
			uni.request({
				url: 'http://www.vzoyo.com/api/user/applyWithdraw',
				method: 'POST',
				header: {
					token: this.token,
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				data: {
					money: that.form.cashMoney
				},
				success(res) {
					if (res.code !== 1) {
						console.log(res.data.msg);
					} else {
						console.log(res.data.msg);
						this.getWithdrawList();
					}
				}
			});
		}
	}
};
